<template>
  <div class="hello">
    <p>全班學生</p>
    <draggable
      v-model="stuList"
      class="elBox"
      group="people"
      @start="drag = true"
      @end="drag = false"
    >
      <div class="dragEl" v-for="element in stuList" :key="element.id">
        {{ element.name }}
      </div>
    </draggable>
    <div
      class="group_box"
      v-for="(item, groupIndex) in groupArr"
      :key="groupIndex"
    >
      <div class="group">
        <p>組別{{ groupIndex + 1 }}</p>
        <draggable
          v-model="item.stu"
          class="elBox"
          group="people"
          @start="drag = true"
          @end="drag = false"
        >
          <div
            class="dragEl"
            v-for="(element, index) in item.stu"
            :key="element.id"
            @click="setLeader(groupIndex, element.stuId)"
            :class="{ leader: Number(item.leader) == Number(element.stuId) }"
          >
            {{ element.name }}
          </div>
        </draggable>
      </div>
    </div>
    <div class="btn" @click="add">新增</div>
  </div>
</template>

<script>
import draggable from "vuedraggable";

export default {
  name: "HelloWorld",
  components: {
    draggable
  },
  data() {
    return {
      stuList: [
        { name: "A", stuId: "01" },
        { name: "B", stuId: "02" },
        { name: "C", stuId: "03" },
        { name: "D", stuId: "04" },
        { name: "E", stuId: "05" },
        { name: "F", stuId: "06" },
        { name: "G", stuId: "07" },
        { name: "H", stuId: "08" },
        { name: "I", stuId: "09" },
        { name: "J", stuId: "10" }
      ],
      groupArr: []
    };
  },
  methods: {
    add() {
      this.groupArr.push({
        stu: []
      });
    },
    setLeader(groupIndex, id) {
      // this.groupArr[groupIndex].leader = id;
      this.$set(this.groupArr[groupIndex], "leader", id);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.hello {
  width: 90%;
  margin: auto;
}
.elBox {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
  border: 1px solid #ddd;
  .dragEl {
    width: calc(25% - 20px);
    font-size: 20px;
    line-height: 40px;
    background-color: #ccc;
    margin: 10px;
    &.leader {
      background-color: #faa;
    }
    @media (max-width: 768px) {
      width: calc(50% - 20px);
    }
  }
}
.btn {
  font-size: 20px;
  padding: 5px 10px;
  margin: 10px;
  border: 1px solid #ccc;
  color: #666;
  display: inline-block;
  cursor: pointer;
  transition: 0.3s;
  &:hover {
    background-color: #ccc;
    color: #fff;
  }
}

h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



